.user{
    display:flex;
    align-items:center;
    margin-top:auto;
    &__photo{
        width:40px;
        height:40px;
        border-radius: 50%;
        overflow: hidden;
        flex-shrink: 0;
    }
    &__img{
        width:100%;
        display:block;
        margin:0 auto;
    }
    &__info{
       display:flex;
       flex-direction: column; 
       padding-left:16px;
    }
    &__name{
        font-size:16px;
        font-weight: 500;
        color:#121212;
        margin-bottom:4px;
    }
    &__email{
        font-size:14px;
        color:#0000009A;
    }
}


.chapter{
    padding-left:0px;
    &__select{
        -webkit-appearance: none;
        -moz-appearance: none;
        border:none;
        font-size:16px;
        color:#121212;
        font-weight: 500;
        line-height:24px;
        margin-right:7px;
        margin-bottom:6px;
    }
    &__select-icon{
        font-size:24px;
        color:#2A8356;
    }
}
.chapter-list{
    border:1px solid #CDDDCD;
    &__item{
        padding:12px;
        border-bottom:1px solid #CDDDCD;
        &--active{
            background-color:#EDF4EE;
        }
        &:first-child{
            border-radius: 2px 2px 0px 0px;
        }
        &:last-child{
            border-radius: 0px 0px 2px 2px;
            border-bottom:none;
        }
    }
    &__header{
        display:flex;
        justify-content: space-between;
        margin-bottom:4px;
    }
    &__content{
        display:flex;
        justify-content: space-between;
        align-items:center;
    }
    &__type{
        font-size:12px;
        color:#121212;
    }
    &__time{
        font-size:12px;
        color:#121212;
    }
    &__info{
        font-size:16px;
        color:#121212;
    }
    &__user{
        font-size:12px;
        color:#0000009A;
    }
}

.chapter-comment-number{
    text-align:right;
    font-size:12px;
    line-height:24px;
    color:#707070;
    margin-bottom:6px;
}
.chapter-comment{
    border:1px solid #CDDDCD;
    border-radius: 2px;
    &__header{
        display:flex;
        align-items:center;
        padding:12px 16px;
        background-color:#EDF4EE;
        border-bottom:1px solid #CDDDCD;
    }
    &__header-lt{
        margin-right:auto;
    }
    &__header-rt{
        margin-left:auto;
    }
    &__type{
        display:block;
        font-size:12px;
        color:#121212;
        margin-bottom:4px;
    }
    &__info{
        font-size:16px;
        color:#121212;
    }
    &__content{
        padding:16px;
        padding-right:32px;
    }
    &__user{
        display:flex;
        padding-bottom:16px;
        &:last-child{
            padding-bottom:0px;
            .chapter-comment__user-content{
                border-bottom:none;
            }
        }
    }
    &__user-icon{
        width:40px;
        height:40px;
        border-radius:50%;
        overflow: hidden;
        flex-shrink: 0;
    }
    &__user-photo{
        display:block;
        width:100%;
    }
    &__user-content{
        width:100%;
        border-bottom:1px solid #CDDDCD;
        padding:0px 0px 16px 8px;
    }
    &__user-header{
        display:flex;
        align-items:center;
    }
    &__user-header-lf{
        margin-right:auto;
    }
    &__user-header-rt{
        margin-left:auto;
    }
    &__user-name{
        display:block;
        font-size:16px;
        color:#121212;
        margin-bottom:4px;
    }
    &__time{
        font-size:12px;
        color:#121212;
    }
    &__user-info{
        font-size:12px;
        color:#121212;
        line-height:21px;
        padding:16px 0px;
        margin:0px;
        ol{
            list-style-type: disc;
            padding:24px;
        }
        .text-mark{
            background-color:#EDF4EE;
            color:#2A8356;
        }
    }
    &__file{
        display:flex;
        align-items: center;
        width:max-content;
        padding:8px 12px;
        background-color:#EDF4EE;
        border:1px solid #CDDDCD;
        border-radius: 2px;
    }
    &__file-title{
        font-size:16px;
        font-weight: 500;
    }
}
.chapter-comment-footer{
    display:flex;
    align-items:center;
    padding-top:16px;
    &__btn{
        margin-left:auto;
        .btn{
            border-radius: 2px;
        }
        .btn-outline-success{
            border-color:#2A8356;
            color:#2A8356;
            &:hover{
                color:white;
                background-color:#2A8356;
            }
        }
        .btn-success{
            background-color:#2A8356;
            border-color:#2A8356;
        }
    }
}