.sidebar-menu{
    padding-top:32px;
    padding-left:8px;
    &__item{
        padding-bottom:31px;
        &--active{
            .sidebar-menu__icon{
                color:#2A8356;
            }
            .sidebar-menu__title{
                color:#2A8356;
            }
        }
    }
    &__link{
        display:flex;
        align-items:center;
        &:hover{
            .sidebar-menu__icon{
                color:#2A8356;
            }
            .sidebar-menu__title{
                color:#2A8356;
            } 
        }
    }
    &__icon{
        font-size:24px;
        color:#121212;
    }
    &__title{
        font-size:16px;
        color:#121212;
        padding-left:16px;
    }
}