.main-header{
    align-items: center;
    &__lf{
        margin-right:auto;
    }
    &__rt{
        margin-left:auto;
    }
    &__title{
        font-size:24px;
        margin-right:46px;
    }
    &__select{
        -webkit-appearance: none;
        -moz-appearance: none;
        border:none;
        font-size:16px;
        color:#121212;
        font-weight: 500;
        margin-right:7px;
    }
    &__select-icon{
        font-size:24px;
        color:#2A8356;
    }
    &__tools{
        .material-icons{
            font-size:24px;
            margin-right:24px;
            &:last-child{
                margin-right:0px;
            }
        }
    }
}